import { useEffect } from "react"
import { PageProps as GatsbyPageProps, graphql, Link } from "gatsby"
import { cx } from "@emotion/css"
import * as Color from "color"

import theme from "../constants/theme"
import { IStyleOverride } from "../data/_componentTypes"
import { mainMenu, contactDetails } from "../data/siteConfig"
import { ISkill, sharedData } from "../data/shared"
import { IService } from "../data/services"
import { IPhoto, IWorkflowStep, aboutData } from "../data/about"

import { Layout, SEO, Button, Section, SubHeaderSection } from "../components"

type Props = GatsbyPageProps & {}

/**
 * About
 */
export default function AboutPage({}: Props) {
  useEffect(() => {}, [])

  return (
    <Layout mainMenu={mainMenu} contactDetails={contactDetails}>
      <SEO title={aboutData.page.title} />
      <SubHeaderSection
        title={aboutData.headerSection.title}
        subTitle={aboutData.headerSection.subTitle}
        titleType="callout"
      />
      <IntroSection photos={aboutData.introSection.photos} />
      <HowSection title={aboutData.howSection.title} steps={aboutData.howSection.steps} />
      <TeamSection />
    </Layout>
  )
}

/* -------------------------------------------------------------------------------- */

/**
 * IntroSection
 */
type IntroSectionProps = IStyleOverride & {
  photos: IPhoto[]
}

function IntroSection({ photos }: IntroSectionProps) {
  return (
    <section
      className={cx("px-20 md:px-40 lg:px-60 xl:px-80", "pb-40 md:pb-60 lg:pb-80", "bg-bg1")}>
      <div className={cx("grid grid-cols-2 gap-10 md:gap-20", "px-1/12 md:px-2/12 xl:px-3/12")}>
        {photos.map(photo => {
          return image(photo.title, photo.imageUrl, photo.resizeCss)
        })}
      </div>
    </section>
  )

  function image(title: string, imageUrl: string, resizeCss?: string) {
    return (
      <div className={cx("aspect-w-1 aspect-h-1")}>
        <div className={cx(resizeCss)}>
          <img
            key={title}
            className={cx("w-full h-full", "rounded-md object-cover")}
            src={imageUrl}
            alt={title}
          />
        </div>
      </div>
    )
  }
}

/* -------------------------------------------------------------------------------- */

/**
 * HowSection
 */
type HowSectionProps = IStyleOverride & {
  title: string
  steps: IWorkflowStep[]
}

function HowSection({ title, steps }: HowSectionProps) {
  return (
    <section
      className={cx(
        "flex flex-col items-center",
        "px-20 md:px-40 lg:px-60 xl:px-80",
        "py-40 md:py-60 lg:py-80",
        "bg-altBg1"
      )}>
      <div className={cx("lg:w-6/12", "mb-40 md:mb-60")}>
        <h2
          className={cx(
            "font-medium text-lg2 md:text-lg3 lg:text-xl text-altText1 text-center",
            "resetLeading"
          )}>
          {title}
        </h2>
      </div>

      <div className={cx("grid grid-cols-1 gap-40 md:gap-60 xl:gap-80")}>
        {steps.map(step => {
          return textBlock(step.count, step.title, step.subTitle, step.text1, step.positionCss)
        })}
      </div>
    </section>
  )

  function textBlock(
    count: string,
    title: string,
    subTitle: string,
    text1: string,
    positionCss?: string
  ) {
    return (
      <div
        className={cx("md:w-10/12 lg:w-7/12 xl:w-6/12", positionCss)}
        css={{
          display: "grid",
          gridTemplateColumns: `40px 1fr`,
          gridTemplateRows: `auto 1fr`,
          gridTemplateAreas: `"count ." ". content"`,
          gap: `10px`,
        }}>
        <p
          css={{ gridArea: `count` }}
          className={cx(
            "text-md2 md:text-md3 lg:text-lg text-accent1 text-right",
            "resetLeading",
            "opacity-20"
          )}>
          {count}
        </p>
        <div
          className="pb-40"
          css={{ gridArea: `content`, borderBottom: `2px solid ${theme.colors.accent2}` }}>
          <h3 className={cx("text-md2 md:text-md3 lg:text-md3 text-text3", "resetLeading", "mb-4")}>
            {title}
          </h3>
          <p className={cx("text-sm md:text-sm2 lg:text-sm3 text-altText1")}>{subTitle}</p>

          <div className="h-20 md:h-30" />

          <p className={cx("text-sm3 md:text-md2 lg:text-md3 text-altText1")}>{text1}</p>
        </div>
      </div>
    )
  }
}

/* -------------------------------------------------------------------------------- */

/**
 * TeamSection
 */
type TeamSectionProps = IStyleOverride & {}

function TeamSection({}: TeamSectionProps) {
  return <section></section>

  function textPane() {}

  function imagePane() {}
}

/**
 * TeamMemberCard
 */
type TeamMemberCardProps = IService & IStyleOverride & {}

function TeamMemberCard({ title, text1, skills }: TeamMemberCardProps) {
  return (
    <div className={cx()}>
      <div className={cx("flex flex-col", "bg-blue-200")}>
        <div>
          <h3 className={cx("text-md md:text-md3 text-text1", "resetLeading")}>{title}</h3>
          <div className="w-80 h-4 bg-accent2"></div>
        </div>

        <p></p>
      </div>
    </div>
  )

  function tags() {}
}
